#errorMessage {
    background-color: rgb(255, 202, 202);
    width: 355px;
    position: fixed;
    right: 10px;
    bottom: 10px;
    padding: 5px;
    text-align: center;
    animation: showFromBottom 2.5s forwards;
  }
  
  #successMessage {
    background-color: rgb(165, 236, 162);
    width: 355px;
    position: fixed;
    right: 10px;
    bottom: 10px;
    padding: 5px;
    text-align: center;
    animation: showFromBottom 2.5s forwards;
  }

  
  @keyframes showFromBottom {
    0% {
      bottom: -50px;
    }
  
    10% {
      bottom: 10px;
    }
  
    90% {
      bottom: 10px;
    }
           
    100% {
      bottom: -80px; 
    }
  }
    