body {
  padding-top: 70px;
  margin: 0;
  font-family: 'Roboto',
  sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#errorMessage {
    background-color: rgb(255, 202, 202);
    width: 355px;
    position: fixed;
    right: 10px;
    bottom: 10px;
    padding: 5px;
    text-align: center;
    -webkit-animation: showFromBottom 2.5s forwards;
            animation: showFromBottom 2.5s forwards;
  }
  
  #successMessage {
    background-color: rgb(165, 236, 162);
    width: 355px;
    position: fixed;
    right: 10px;
    bottom: 10px;
    padding: 5px;
    text-align: center;
    -webkit-animation: showFromBottom 2.5s forwards;
            animation: showFromBottom 2.5s forwards;
  }

  
  @-webkit-keyframes showFromBottom {
    0% {
      bottom: -50px;
    }
  
    10% {
      bottom: 10px;
    }
  
    90% {
      bottom: 10px;
    }
           
    100% {
      bottom: -80px; 
    }
  }

  
  @keyframes showFromBottom {
    0% {
      bottom: -50px;
    }
  
    10% {
      bottom: 10px;
    }
  
    90% {
      bottom: 10px;
    }
           
    100% {
      bottom: -80px; 
    }
  }
    
#dropdown-autoclose-inside {
    width: 40vw;
    background-color: transparent;
    border: none;
    height: 50px;
    color: transparent;
}

#dropdown-autoclose-inside:focus,
#dropdown-autoclose-inside:active {
    outline: none !important;
    box-shadow: none;
}

.navbar {
    background: rgba(51, 169, 141, 0.9);
}

.navbar__logo {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    color: #435151 !important;
}

.navbar__avatar {
    margin-left: 2px;
    width: 25px;
    height: 25px;
    object-fit: cover;
}

.navbar a {
    text-decoration: none;
}

.navbar__cart {
    margin: 0;
}

.navbar__cart p {
    color: #fff;
    margin: 0;
    font-size: 14px;
    font-family: 'Poppins',
    sans-serif;
    font-weight: 700;
}

.navbar__cart__collapsed {
    color: rgba(255, 255, 255, .55) !important;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
}

.navbar__cart__collapsed:hover {
    color: #fff !important;
}

@media (max-width: 992px) {
    .navbar__cart {
        display: none !important;
    }
}

@media (min-width: 992px) {
    .navbar__cart__collapsed {
        display: none !important;
    }
}
.searchbar__input {
    font-family: 'Poppins',
    sans-serif;
}

.searchbar__input:focus {
    box-shadow: none !important;
}
.navbar__large__text {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    color: #fff
}

.navbar__collapsed__text {
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
}

@media (max-width: 992px) {
    .navbar__large  {
        display: none !important;
    }

}

@media (min-width: 992px) {
    .navbar__collapsed {
        display: none;
    }
}
.dropdown__menu img {
    width: 100%;
    height: 40px;
    object-fit: cover;
}



