.homepage {
    font-family: 'Poppins', sans-serif;
    background: #EFEBE3;
    border-radius: 54px;
}

.homepage h1 {
    font-weight: 700;
}

.homepage h3 p {
    font-weight: 400;
}

.homepage__btn {
    padding: 8px;
    background: #318161;
    border: 3px solid #318161;
    border-radius: 14px;
    color: #fff;
    font-weight: 500;
}

.about img {
    width: 80%;
}

.about p {
    font-size: 1.3rem;
}


@media (max-width: 768px) {
    .homepage__row {
        background-color: #fff
    }
    .homepage__col__text {
        margin-bottom: 2vh;
    }
    .homepage img {
        width: 50%;
        display: flex;
        margin-left: auto;
        margin-right: auto;
        margin-top: 3vh;
    }

    .about img {
        display: none;
    }
}

@media (max-width: 992px) {
    .homepage__row {
        background-color: #fff;
    }
    .homepage__col__text {
        margin-bottom: 2vh;
    }
    .homepage img {
        width: 80%;
        display: flex;
        margin-left: auto;
        margin-right: auto;
        margin-top: 3vh;
    }

    .about img {
        display: none;
    }

    .about h2, .about p {
        text-align: center;
    }
}

@media (min-width: 992px) {
    .homepage__row {
        background: rgba(255, 255, 255, 0.25);
        box-sizing: border-box;
        box-shadow: 0px 50px 50px #DFD9CE;
        border-radius: 54px;
    }

    .homepage img {
        width: 100%;
    }
}

footer {
    background: rgba(51, 169, 141, 0.9);
    bottom: 0;
}
.signup__avatar {
    width: 10vh;
    height: 10vh;
    object-fit: cover;
    margin-right: 3vh;

}

.signup__uploadavatar {
    padding: 5px 10px;
    transition: -webkit-transform .6s;
    transition: transform .6s;
    transition: transform .6s, -webkit-transform .6s;
    margin-left: 20px !important;
}

.signup__uploadavatar:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: .7;
}

.form-control-hide {
   display: none;
}
.profile {
    margin-top: 70px;
}


.profile__col {
    border-radius: 8px;
    border: 0.1px solid #318161
}

@media (max-width: 768px) {
    .profile img {
        object-fit: cover;
        width: 10vh;
        height: 10vh;
    }
}

@media (max-width: 992px) {
    .profile img {
        object-fit: cover;
        height: 75px;
        width: 75px;
    }
}

@media (min-width: 992px) {
    .profile img {
        object-fit: cover;
        height: 100px;
        width: 100px;
    }
}
.sell__form {
    margin-top: 40px;
}
#starRadio {
    display: none;
}

.star {
    cursor: pointer;
    transition: color 200ms;
}
.send__message img {
    width: 10%;
    border-radius: 50%;
}
.modal__title {
    font-family: "Poppins", "sans-serif";
}

.close {
    background-color: white;
    border: 1px solid #318161;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 7px;
    color: #318161;
}

.close:hover {
    background-color: #318161;
    border: 1px solid white;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 7px;
    color: white;
}
.plantslist {
    font-family: "Poppins", "sans-serif";
}

.plantslist h1 {
    margin-top: 2vh;
    margin-bottom: 3vh;
    font-size: 3rem;
}

.plantslist_imagecontainer{
    overflow: hidden;
    border: 1px solid ;
}

.plantslist img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    opacity: 1;
}

.plantslist img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: .7;
}

@media (max-width: 768px) {
    .plantslist .btn-success {
        margin-bottom: 1vh;
        width: 40%;
        padding: 10px;
    }

    .plantslist .btn-outline-success {
        margin-top: 1vh;
        width: 40%;
        padding: 10px;
    }
}

@media (max-width: 992px) {
    .plantslist .btn-success {
        margin-bottom: 1vh;
        width: 70%;
    }
    .plantslist .btn-outline-success {
        margin-top: 1vh;
        width: 70%;
    }
}
.cart {
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;

}

.cart h2 {
    margin-bottom: 1rem;
}

.cart__left {
    flex: 0.7 1;
    margin-right: 1rem;
    background-color: transparent;
    padding: 1rem;
}

.cart__right {
    flex: 0.3 1;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0,0,0,0.4);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.cart__right div {
    border-bottom: 1px, solid, rgba(0,0,0,0.2);
    padding: 1rem;
}

.cart__right div:last-child{
    border: none;
}

.cart__info div {
    padding: 8px;
}

.cart__right div button {
    padding: 10px 17px;
    width: 100%;
    background-color: #171717;
    color: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
}

.cart__right div button:hover{
    opacity: 0.9;
}

@media (max-width: 1320px) {
    .cart {
        max-width: 900px;
    }
}

@media (max-width: 960px) {
    .cart {
        max-width: 800px;
        flex-direction: column;
    }

    .cart__left {
        margin: 0;
    }

    .cart__right {
        margin: 1rem;
    }
}
.cartitem__img {
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.cartitem {
    width: 100%;
    padding: 1rem;
    margin-bottom: 8px;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
    grid-gap: 8px;
    gap: 8px;
    background-color: #fff;
    border-radius: 2px;
    place-items: center;
}

.cartitem__image img {
    width: 70px;
    height: 70px;
    object-fit: cover;
    border-radius: 50%;
}

.cartitem__name {
    text-decoration: none;
    color: #171717;
}

.cartitem__name:hover{
    color: #dd219e;
}

.cartitem__select{
    padding: 10px 17px;
}

.cartitem__deletebtn{
    padding: 10px 17px;
    color: red;
    background: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
    transition: all 0.3s ease-out; 
}

.cartitem__deletebtn:hover,
.cartitem__deletebtn:focus,
.cartitem__deletebtn:active {
    background: #171717;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@media (max-width: 700px) {
    .cartitem__name {
        font-size: 0.8rem;
    }

    .cartitem__select,
    .cartitem__deletebtn {
        padding: 8px 13px
    }

    .cartitem__price {
        font-size: 0.8rem
    }
}

@media (max-width: 500px) {
    .cartitem__name {
        font-size: 0.6rem;
    }

    .cartitem__select,
    .cartitem__deletebtn {
        padding: 5px 8px
    }

    .cartitem__price {
        font-size: 0.6rem
    }
}

.messages__container {
    margin-top: 70px;
}

.no__message {
    text-align: center;
    font-size: 20px;
    margin-top: 20vh;
    margin-bottom: 20vh;
    font-family: "Poppins", "sans-serif";
}

.messages__avatar {
    width: 30%;
